import React, { PureComponent, Fragment } from 'react';

import { studentAvailableForOffBudgetList } from 'dump/data';

import BtnSave from 'components/common/button/BtnSave';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Table, Button, Col, Form } from 'antd';
import FormOffBudgetStudent from './FormOffBudgetStudent';

const studentColumns = type => [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => {
      if (type === 'remove') {
        return <Button type="danger">Xoá</Button>;
      }
      return <Button type="primary">Chọn</Button>;
    }
  },
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và Tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày Sinh',
    dataIndex: 'dateOfBirth',
    key: 'dateOfBirth',
  },
  {
    title: 'Tên Lớp',
    dataIndex: 'className',
    key: 'className',
  },
  {
    title: 'Nơi Sinh',
    dataIndex: 'placeOfBirth',
    key: 'placeOfBirth',
  },
];

class ListOffBudgetStudentPage extends PureComponent {
  state = {};

  render() {
    return (
      <Fragment>
        <Card title="Chọn sinh viên ngoài ngân sách">
          <Row>
            <Col span={24}>
              <FormOffBudgetStudent />
            </Col>
          </Row>
          <Row>
            {/* <Col span={24}>
              <SearchBar />
            </Col> */}
            <Col span={24}>
              <Table
                bordered
                dataSource={studentAvailableForOffBudgetList}
                columns={studentColumns()}
              />
            </Col>

          </Row>
        </Card>

        <Card title="Danh sách sinh viên ngoài ngân sách">
          <Form style={{ textAlign: 'right' }} onSubmit={this.handleSubmit} layout="inline">
            <Form.Item> <BtnSave htmlType="submit" /></Form.Item>
          </Form>
          <Row>
            <Col span={24}>
              <Table
                bordered
                dataSource={studentAvailableForOffBudgetList}
                columns={studentColumns('remove')}
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListOffBudgetStudentPage />
  </Page>
);



